import React from "react"
import GeneralLayout from "../layouts/general-layout"
import { graphql } from 'gatsby'
import Helmet from "react-helmet"

export default function BlogPost({data}){    
    return <GeneralLayout>
        <Helmet>
            <title>{data.blogPost.name} - Savvas Stephanides</title>
        </Helmet>
        <div className="blog-post content-width">
          <h1>{data.blogPost.name}</h1>
          <div id="blog-post-content" className="content-width" dangerouslySetInnerHTML={{__html: data.blogPost.content}}></div>
        </div>
        
        
    </GeneralLayout>
}

export const query = graphql`
query ($slug: String!) {
    blogPost(slug: {eq: $slug}) {
      name
      content
    }
  }
`